.card {
    display: flex;
    flex-flow: row;
    width: 700px;
    @include boxshadow();
    margin: 0 1.5em 3em 1.5em;

    @media (max-width: 520px) {
        margin-left: 1em;
        margin-right: 1em; }

    &__description {
        display: flex;
        flex-flow: column;
        padding: 1.75em 1em 1.5em 2em;

        @media (max-width: 640px) {
            width: 100%; }

        &__tag {
            font-family: 'Georgia';
            color: rgba(250, 200, 0, 1);
            font-size: .9375em;
            letter-spacing: 1px;
            margin: 0 0 .6em 0; }

        &__date {
            padding-left: .5em;
            opacity: .5; }

        &__title {
            font-family: 'Montserrat';
            font-weight: bold;
            font-size: 1.55em;
            letter-spacing: 1px;
            color: $grey;
            text-transform: uppercase; }

        &__anchor {
            color: $text;
            font-family: 'Georgia';
            line-height: 1.4;

            &__wrapper {
                display: flex;
                flex-flow: row;
                align-items: center;
                margin: .3em 0 0 0; } } }

    &__showcase {
        flex: 0 0 35%;
        background-color: #E7E7E7;
        overflow: hidden;
        background-repeat: no-repeat;
        background-position: center;

        @media (max-width: 585px) {
            display: none; } }

    &__dot {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        background-color: #fff;
        height: 35px;
        width: 35px;
        border-radius: 50%;
        margin-top: -12px;
        margin-left: -12px;
        box-shadow: 0px 0px 21px 0px rgba(0, 0, 0, .18);
        cursor: pointer;
        @include animate();

        &:hover {
            height: 41px;
            width: 41px;
            margin-top: -15px;
            margin-left: -15px;
            @include animate(); } }

    &--inactive {
        display: none !important; }

    &--highlight {
        width: 1020px; } }
