.nav {
    position: relative;
    flex: 0 0 100%;
    display: flex;
    justify-content: space-between;
    height: 50%;
    z-index: 1;

    @media (max-width: 520px) {
        height: 30%; }

    &__brand {
        width: 151px;
        padding-top: .5em;

        @media (max-width: 660px) {
            width: 120px; }

        @media (max-width: 520px) {
            width: 105px; }

        @media (max-width: 370px) {
            width: 85px; } }

    &__navigation {
        height: 75px;
        display: flex;
        align-items: center;

        &__item {
            font-family: 'Montserrat';
            font-weight: 500;
            color: #fefefe;
            text-transform: uppercase;
            font-size: 13px;
            letter-spacing: 4px;
            white-space: nowrap;
            @include animate();

            @media (max-width: 1100px) {
                font-size: 12px;
                letter-spacing: 3px;
                padding-left: 1em;
                padding-right: 1em; }

            &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4) {
                @media (max-width: 830px) {
                    display: none; } }

            &:hover {
                background-color: rgba(255, 255, 255, 0.07);
                @include animate(); }

            &--cta {
                margin: 0 0 0 1.5em;
                text-transform: none;
                letter-spacing: 1px;
                white-space: nowrap;

                @media (max-width: 520px) {
                    margin-left: 0; }

                &:hover {
                    @include animate();
                    background-color: $darkness; } } }


        & .lang {
            margin-left: 2em; } }

    &--fixed {
        position: fixed;
        justify-content: center;
        width: 100%;
        height: 57px;
        padding: 0 5em;
        top: 0;
        left: 0;
        background: linear-gradient(#f1f1f1 0%, rgba(255,255,255,0) 172%);
        -webkit-backdrop-filter: saturate(120%) blur(20px);
        backdrop-filter: saturate(120%) blur(20px);
        z-index: 99999999;

        & .nav__navigation {
            height: 55px;

            &__item {
                color: #2f2f2f;
                display: block !important;

                &--cta:hover {
                    color: white; }

                &:last-child {
                    display: none; }

                @media (max-width: 630px) {
                    font-size: 11px;
                    letter-spacing: 1px;
                    padding-left: .8em;
                    padding-right: .8em; }

                @media (max-width: 375px) {
                    font-size: 10px;
                    letter-spacing: 1px; }

                @media (max-width: 340px) {
                    font-size: 9px;
                    letter-spacing: 1px;
                    padding-left: .5em;
                    padding-right: .5em; } }

            &--de {

                @media (max-width: 911px) {

                    & a:nth-child(6) {
                        display: none !important; } }

                @media (max-width: 732px) {

                    & a:nth-child(1), a:nth-child(2), a:nth-child(3), a:nth-child(4), a:nth-child(6) {
                        display: none !important; } } } } } }
